import React, { useState, useEffect } from "react";
import noteService from "./services/notes";

const Filter = ({ filter, filterF, handleChangeFilter }) => {
  return (
    <form onSubmit={filterF}>
      <div>
        Filter shown with <input value={filter} onChange={handleChangeFilter} />
      </div>
    </form>
  );
};
const AddNote = ({
  addNote,
  newName,
  handleChange,
  newNumber,
  handleChangeNum,
}) => {
  return (
    <form onSubmit={addNote}>
      <div>
        name: <input value={newName} onChange={handleChange} />
      </div>
      <div>
        number: <input value={newNumber} onChange={handleChangeNum} />
      </div>
      <div>
        <button type="submit">add</button>
      </div>
    </form>
  );
};

const Numbers = ({ persons, filter, deletePerson }) => {
  const temp = persons.filter((person) => person.name.includes(filter));
  return (
    <ul>
      {temp.map((person) => (
        <li key={person.id}>
          {person.name} {person.number}{" "}
          <button onClick={() => deletePerson(person.id)}> Delete </button>
        </li>
      ))}
    </ul>
  );
};

const App = () => {
  const [persons, setPersons] = useState([
    { id: 1, name: "Arto Hellas", show: true },
  ]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState(0);
  const [filter, setFilter] = useState("");

  const handleChange = (event) => {
    setNewName(event.target.value);
  };
  const handleChangeNum = (event) => {
    setNewNumber(event.target.value);
  };
  const handleChangeFilter = (event) => {
    setFilter(event.target.value);
  };
  const addNote = (event) => {
    event.preventDefault();
    const temp = persons.filter((person) => person.name === newName);
    if (temp.length > 0) {
      const obj = {
        id: temp[0].id,
        name: newName,
        number: newNumber,
      };
      noteService.update(temp[0].id, obj).then((response) => {
        setPersons(
          persons.map((person) =>
            person.id !== temp[0].id ? person : response
          )
        );
      });
      return;
    }
    const obj = {
      id: persons.length + 1,
      name: newName,
      number: newNumber,
    };
    noteService.create(obj).then((returnedNote) => {
      setPersons(persons.concat(returnedNote));
      setNewName("");
      setNewNumber(0);
    });
  };

  const filterF = (event) => {
    event.preventDefault();
  };

  useEffect(() => {
    noteService.getAll().then((person) => {
      setPersons(person);
    });
  }, []);

  const deletePerson = (id) => {
    if (
      window.confirm("Are you sure you want to delete this contact") === true
    ) {
      noteService.deletePerson(id).then((response) => {
        setPersons(persons.filter((person) => person.id !== id));
      });
    }
  };

  return (
    <div>
      <h2>Phonebook</h2>
      <Filter
        filter={filter}
        filterF={filterF}
        handleChangeFilter={handleChangeFilter}
      />
      <h2>Add New Contacts</h2>
      <AddNote
        addNote={addNote}
        newName={newName}
        handleChange={handleChange}
        newNumber={newNumber}
        handleChangeNum={handleChangeNum}
      />
      <h2>Numbers</h2>
      <Numbers persons={persons} filter={filter} deletePerson={deletePerson} />
    </div>
  );
};

export default App;
